@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;1,500&display=swap');
body{
    background-color: #0a192f
}
@layer base{
    body{
        @apply font-[Raleway];

    }
    li{
        @apply px-4;
        @apply cursor-pointer
    }
}
body[data-aos-duration='4000'] [data-aos], [data-aos][data-aos][data-aos-duration='4000']{
    transition-duration: 4000ms;
  }
